import React, { useEffect, useState } from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"
import './layout.scss';

const NavItem = ({children}) => {
  const onMouseOver = (e) => {
    e.target.querySelector('span').className = 'arrow';
  }
  const onMouseOut = (e) => {
    e.target.querySelector('span').className = 'bullet';
  }

  return (
    <div onMouseOver={onMouseOver} onMouseOut={onMouseOut}><span className="bullet"></span>{children}</div>
  )
} 

const Checkbox = (props) => {
  return (
    <label>
      <input type="checkbox" className="nes-checkbox" {...props}/>
      <span>{props.label}</span>
    </label>
  );
};

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  const onAnimationEnd = () => {
    
  };

  const [animate, toggleAnimate] = useState(true);
  useEffect(() => {
    if (animate) {
      document.body.style.animationPlayState = "running";
    } else {
      document.body.style.animationPlayState = "paused";
    }
  }, [animate])

  return (
    <>
      <div className="nes-container is-rounded is-dark nav">
        <NavItem>Hi</NavItem>
        <NavItem>Bye</NavItem>
      </div>
      <div><Checkbox label="Animate Background?" checked={animate} onChange={() => toggleAnimate(!animate)} /></div>
      {children}
    </>
  )
}

